	module.exports = {"data":{"markdownRemark":{"html":"<h1>If Else Statement</h1>\n<p>The If-Else statement executes a block of code depending if your precondition is fullfilled or not.</p>\n<h2>Example</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>int Price = 30;\n\nIf (Price = 30)\n{\n  Console.WriteLine(\"Price is equal to 30.\");\n}\n\nElse \n{\n  Console.WriteLine(\"Price is not equal to 30.\");\n}</code></pre>\n      </div>\n<p>Since we already declared our int Price to be 30, this will be the expected output.</p>\n<h2>Output</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>Price is equal to 30.</code></pre>\n      </div>","fields":{"slug":"/csharp/if-else-statement/"},"frontmatter":{"title":"If Else Statement"}}},"pathContext":{"slug":"/csharp/if-else-statement/"}}

